import moment from 'moment';
import _ from 'lodash';
import Player from '../models/Player';

import { OpenBet, HistoryBet } from '../models/BetOrder';

import updatePlayerStatusAfterOrder from './updateUser/updatePlayerStatusAfterOrder';

import confirmResultStraight from './confirmOrderTypeResult/confirmResultStraight'
import confirmResultParlay from './confirmOrderTypeResult/confirmResultParlay'
import confirmResultTeaser from './confirmOrderTypeResult/confirmResultTeaser'
import confirmResultReverse from './confirmOrderTypeResult/confirmResultReverse'

const confirm_OpenBet_result_to_HistoryBet = async () => {

	try{
		const openBets = await OpenBet.find({})
		if(_.isEmpty(openBets)){
			next()
		}

		await Promise.all(openBets.map(async openBet => {

			switch(openBet.orderType){
				case 'Straight':
					const straightBetResult = await confirmResultStraight(openBet)
					if(straightBetResult === true){
						return JSON.parse(JSON.stringify(openBet.owner.player))
					}else{
						return null
					}
					break;
				case 'Parlay':
					const parlayBetResult = await confirmResultParlay(openBet)
					if(parlayBetResult === true){
						return JSON.parse(JSON.stringify(openBet.owner.player))
					}else{
						return null
					}
					break;
				case 'Teaser6040':
				case 'Teaser6545':
				case 'Teaser7050':
				case 'SuperTeaser':
					const teaserBetResult = await confirmResultTeaser(openBet)
					if(teaserBetResult === true){
						return JSON.parse(JSON.stringify(openBet.owner.player))
					}else{
						return null
					}
					break;
				case 'WinReverse':
				case 'ActionReverse':
					const reverseBetResult = await confirmResultReverse(openBet)
					if(reverseBetResult === true){
						return JSON.parse(JSON.stringify(openBet.owner.player))
					}else{
						return null
					}
					break;
				default:
					return null
			}

		})).then(async (playerIds) => {
			const playerIdArr = _.compact([ ...new Set(playerIds) ])
			if(!_.isEmpty(playerIdArr)){
				console.log('update following players')
				console.log(playerIdArr)
				await Promise.all(playerIdArr.map(async playerId => {
					await updatePlayerStatusAfterOrder(playerId)
					console.log('updated' + playerId)
					return null
				})).catch(err => { throw err })
			}else{
				console.log('no players need to update')
			}
		}).catch(err => { throw err })

	}catch(err){
		throw err
	}

}

export default confirm_OpenBet_result_to_HistoryBet



